* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html,
body {
  font-family: "Manrope", sans-serif;
  scroll-behavior: smooth;
  font-weight: normal;
  line-height: normal;
  letter-spacing: normal;
  background-color: #F0F0F0;
  scroll-behavior: smooth;
}
html button,
body button {
  cursor: pointer;
}

.close-modal__body {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
  z-index: -3;
  transition: ease-in-out 0.2s;
}

.close-modal__btn {
  position: fixed;
  background: local;
  border: none;
  top: 20px;
  right: 20px;
  visibility: hidden;
  z-index: -4;
  transition: ease-in-out 0.2s;
  opacity: 0;
}

.close-modal__btn--active {
  z-index: 20;
  visibility: visible;
  opacity: 1;
}

.home-modal {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 20px;
  background-color: #21422F;
  border-color: transparent;
  border-style: solid;
  width: fit-content;
  height: fit-content;
  padding: 25px;
  opacity: 0;
  visibility: hidden;
  z-index: -5;
  transition: ease-in-out 0.2s;
}
.home-modal h1 {
  max-width: 330px;
  text-align: center;
  color: #ffffff;
  font-size: 22px;
  font-family: "inter", sans-serif;
  line-height: 1.3;
  font-weight: 300;
  background-position: center center;
  border-color: transparent;
  border-style: solid;
  margin-bottom: 20px;
}
@media (max-width: 700px) {
  .home-modal h1 {
    max-width: 100%;
  }
}
.home-modal .home-modal__social {
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 10px;
}
.home-modal .home-modal__social a {
  display: block;
  width: 42px;
  height: 42px;
}
.home-modal .home-modal__social a:last-child {
  width: 48px;
  height: 48px;
}
.home-modal .home-modal__social a img {
  width: 100%;
  height: 100%;
}

.closeModal--active {
  z-index: 14;
  opacity: 1;
  visibility: visible;
}

