    flex-direction: column;
    align-items: start;
    row-gap: 10px;
  }
}
.footer-bottom p {
  color: #959595;
  font-size: 14px;
  font-weight: 300;
  letter-spacing: 0.42px;
}
.footer-bottom a {
  width: 152px;
  height: 44px;
  flex-shrink: 0;
  display: block;
}

.footer-left {
  display: flex;
  align-items: start;
  flex-direction: column;
  row-gap: 20px;
}
.footer-left h5 {
  max-width: 230px;
  color: #959595;
  font-family: "EurostileExtended-Roman-DTC", sans-serif;
  font-size: 14px;
  font-weight: 5;
  letter-spacing: 0.42px;
  text-transform: uppercase;
}
.footer-left .footer-left__links {
  display: flex;
  align-items: start;
  column-gap: 20px;
}
@media (max-width: 520px) {
  .footer-left .footer-left__links {
    display: none;
  }
}
.footer-left .footer-left__links a {
  color: #000;
  font-family: "EurostileExtended-Roman-DTC", sans-serif;
  font-size: 14px;
  font-weight: 5;
  text-decoration: none;
}
.footer-left .footer-logo {
  width: 166.511px;
  height: 49.391px;
  flex-shrink: 0;
  display: block;
}
.footer-left .footer-logo img {
  width: 100%;
  height: 100%;
}

.footer-right .footer-right__title {
  color: #959595;
  font-family: "EurostileExtended-Roman-DTC", sans-serif;
  font-size: 14px;
  max-width: 238px;
  font-weight: 5;
}
.footer-right .footer-right__bottom-title {
  max-width: 100%;
  margin-top: 20px;
}
.footer-right .footer-right__bottom-title span {
  color: #000;
}
.footer-right .footer-right__contact {
  display: flex;
  align-items: center;
  column-gap: 50px;
  margin: 20px 0;
}
@media (max-width: 1240px) {
  .footer-right .footer-right__contact {
    column-gap: 20px;
  }
}
@media (max-width: 520px) {
  .footer-right .footer-right__contact {
    margin: 10px 0;
    flex-direction: column;
    align-items: start;
    justify-content: center;
    row-gap: 10px;
  }
}
.footer-right .footer-right__contact .footer-right__call {
  color: #000;
  font-family: "EurostileExtended-Roman-DTC", sans-serif;
  font-size: 18px;
  font-weight: 5;
  text-decoration: none;
}
.footer-right .footer-right__contact .footer-reight__social {
  display: flex;
  align-items: center;
  column-gap: 10px;
}
@media (max-width: 520px) {
  .footer-right .footer-right__contact .footer-reight__social a {
    display: block;
    width: 30px;
    height: 30px;
  }
  .footer-right .footer-right__contact .footer-reight__social a img {
    width: 100%;
    height: 100%;
  }
  .footer-right .footer-right__contact .footer-reight__social a:last-child {
    width: 36px;
    height: 36px;
  }
}
.footer-right .footer-right__contact .footer-reight__social a {
  display: block;
  text-decoration: none;
}
.footer-right .footer-right__contact .footer-right__backCall {
  display: flex;
  margin-left: auto;
  color: #FFF;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.56px;
  background-color: #21422F;
  text-decoration: none;
  border-radius: 20px;
  border: none;
  padding: 5px 25px;
  transition: ease-in-out 0.2s;
}
@media (max-width: 1200px) {
  .footer-right .footer-right__contact .footer-right__backCall {
    margin-left: 0;
  }
}
.footer-right .footer-right__contact .footer-right__backCall:hover {
  background-color: #5d382e;
}

#sale-table {
  margin-top: 60px;
  overflow-x: scroll;
}
#sale-table .sale-table__container {
  max-width: calc(100% - 150px);
  margin: 0 auto;
}
@media (max-width: 1070px) {
  #sale-table .sale-table__container {
    max-width: 90%;
  }
}
#sale-table::-webkit-scrollbar {
  display: none;
}

.sale-table__bottom-btn {
  margin-top: 40px;
  width: 100%;
  color: #FFF;
  font-family: Manrope;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.56px;
  background-color: #21422F;
  border: none;
  border-radius: 20px;
  padding: 10px;
  transition: ease-in-out 0.2s;
}
.sale-table__bottom-btn:hover {
  background-color: #5d382e;
}

.sale-table__head {
  padding-bottom: 20px;
  border-bottom: 1px solid #C5C5C5;
}
.sale-table__head .row > div {
  display: flex;
  align-items: end;
}
.sale-table__head .wrap-item-group {
  position: relative;
  cursor: pointer;
}
.sale-table__head .wrap-item-group .number_menu {
  position: absolute;
  top: calc(100% + 5px);
  overflow-y: scroll;
  height: 150px;
  left: 0;
  z-index: 3;
  transform: translateX(0px);
}
.sale-table__head .wrap-item-group .number_menu::-webkit-scrollbar {
  display: none;
}
.sale-table__head .wrap-item-group .number_menu li {
  cursor: pointer;
  font-size: 14px;
  line-height: 170%;
  text-align: start;
}
@media (max-width: 1070px) {
  .sale-table__head {
    min-width: 700px;
  }
}
.sale-table__head .head__title {
  color: #000;
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 0.48px;
  text-transform: capitalize;
}

@media (max-width: 1070px) {
  .sale-table__body {
    min-width: 700px;
  }
}
.sale-table__body .table__body-item {
  padding: 20px 0;
  border-bottom: 1px solid #C5C5C5;
  transition: ease-in-out 0.2s;
}
.sale-table__body .table__body-item:hover {
  background-color: #21422F;
}
.sale-table__body .table__body-item:hover .sale-table__region, .sale-table__body .table__body-item:hover .sale-table__price {
  color: white;
}
.sale-table__body .table__body-item:hover .sale-teble__btn {
  background-color: #fff;
  color: #5d382e;
}
.sale-table__body .table__body-item .row > div {
  display: flex;
  align-items: center;
}

.item-car__number {
  border-radius: 5px;
  background-color: #000;
  width: fit-content;
  display: flex;
}
.item-car__number .number__left {
  background-color: #F8F8F8;
  border-radius: 5px;
  border: 3px solid #000;
  padding: 0 10px;
  display: flex;
  align-items: end;
  column-gap: 5px;
}
.item-car__number .number__left h2 {
  color: #000;
  font-family: "readnumber", sans-serif;
  font-size: 35px;
  font-weight: 400;
  letter-spacing: 1.2px;
  text-transform: uppercase;
}
.item-car__number .number__left .item-min__number {
  display: flex;
  align-items: end;
}
.item-car__number .number__left .item-min__number h1 {
  color: #000;
  font-family: "readnumber", sans-serif;
  font-size: 40px;
  font-weight: 400;
  letter-spacing: 2px;
  text-transform: uppercase;
}
.item-car__number .number__right {
  border-radius: 5px;
  width: 50px;
  flex-shrink: 0;
  border: 3px solid #000;
  border-left: none;
  background-color: #F8F8F8;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.item-car__number .number__right .right-country {
  display: flex;
  align-items: center;
}
.item-car__number .number__right .right-country h4 {
  color: #000;
  font-family: "inter", sans-serif;
  font-size: 10px;
  font-weight: 600;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}
.item-car__number .number__right .right-country img {
  width: 16px;
  height: 8px;
  flex-shrink: 0;
}

.sale-table__region, .sale-table__price {
  color: #000;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.56px;
}

.sale-teble__btn {
  border-radius: 20px;
  background: #21422F;
  border: none;
  color: #FFF;
  font-family: Manrope;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.56px;
  padding: 10px 0;
  width: 100%;
  transition: ease-in-out 0.2s;
}