* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

@mixin media($bp) {
	@media (max-width: $bp) {
		@content;
	}
}

$fontTwo: 'EurostileExtended-Roman-DTC', sans-serif;
$fontThree: 'readnumber', sans-serif;
$inter: 'inter', sans-serif;

html,
body {
	font-family: 'Manrope', sans-serif;
	scroll-behavior: smooth;
	font-weight: normal;
	line-height: normal;
	letter-spacing: normal;
	background-color: #F0F0F0;
	scroll-behavior: smooth;

	button {
		cursor: pointer;
	}
}

.close-modal__body {
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	background: rgba(0, 0, 0, 0.50);
	opacity: 0;
	visibility: hidden;
	z-index: -3;
	transition: ease-in-out 0.2s;
}

.close-modal__btn {
	position: fixed;
	background: local;
	border: none;
	top: 20px;
	right: 20px;
	visibility: hidden;
	z-index: -4;
	transition: ease-in-out 0.2s;
	opacity: 0;
}

.close-modal__btn--active {
	z-index: 20;
	visibility: visible;
	opacity: 1;
}

.home-modal {
	position: fixed;
	width: 100%;
	height: 100%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	border-radius: 20px;
	background-color: #21422F;
	border-color: transparent;
	border-style: solid;
	width: fit-content;
	height: fit-content;
	padding: 25px;
	opacity: 0;
	visibility: hidden;
	z-index: -5;
	transition: ease-in-out 0.2s;

	h1 {
		max-width: 330px;
		text-align: center;
		color: #ffffff;
		font-size: 22px;
		font-family: $inter;
		line-height: 1.3;
		font-weight: 300;
		background-position: center center;
		border-color: transparent;
		border-style: solid;
		margin-bottom: 20px;

		@include media(700px) {
			max-width: 100%;
		}
	}

	.home-modal__social {
		display: flex;
		align-items: center;
		justify-content: center;
		column-gap: 10px;

		a {
			display: block;
			width: 42px;
			height: 42px;

			&:last-child {
				width: 48px;
				height: 48px;
			}

			img {
				width: 100%;
				height: 100%;
			}
		}
	}
}

.closeModal--active {
	z-index: 14;
	opacity: 1;
	visibility: visible;
}

.container {
	max-width: 1160px;
	margin: 0 auto;

	@include media(1240px) {
		max-width: 1000px;
	}

	@include media(1240px) {
		max-width: 1000px;
	}

	@include media(1070px) {
		max-width: 768px;
	}

	@include media(850px) {
		max-width: 576px;
	}

	@include media(650px) {
		max-width: 100%;
		padding: 0 20px;
	}
}

.home-modal.active {
	z-index: 15;
	visibility: visible;
	opacity: 1;

	@include media(700px) {
		width: 100%;
		max-width: 90%;
	}
}

// header start
#header {
	position: fixed;
	z-index: 4;
	width: 100%;
	padding: 20px 0;
	top: 0;
	left: 0;
	backdrop-filter: blur(20px);

	&::before {
		content: '';
		opacity: 0.5;
		background-color: #1c1c1c;
		position: absolute;
		top: 0;
		width: 100%;
		height: 100%;
		z-index: -1;
	}
}

nav {
	display: none;

	a.logo {
		width: 124px;

		img {
			width: 100%;
		}
	}

	@include media(850px) {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
}

.navbar-menu {
	transition: ease-in-out 0.5s;
	position: fixed;
	z-index: 99999;
	top: 0;
	left: -100%;
	opacity: 0;
	visibility: hidden;
	width: 100%;
	max-width: 320px;
	height: 100%;
	background-color: #21422F;
	padding: 0 50px;
	padding-top: 150px;

	.navbar-menu__btn {
		position: absolute;
		top: 20px;
		right: 20px;
		border: none;
		background: local;
	}

	.navbar-menu__content {
		width: 100%;
		padding-bottom: 30px;
		margin-bottom: 30px;
		border-bottom: 1px solid #fff;

		li {
			list-style: none;

			&:not(:first-child) {
				margin-top: 25px;
			}
		}
	}

	p {
		color: #959595;
		font-size: 14px;
		font-style: normal;
		font-weight: 300;
		line-height: normal;
		letter-spacing: 0.42px;
	}
}

.navbar-menu.active {
	left: 0;
	visibility: visible;
	opacity: 1;
}

.navbar-menu__content {
	li a {
		color: #e8e8e8;
		font-size: 30px;
		font-family: $fontTwo;
		font-weight: 400;
		text-decoration: none;
	}
}

.media-navbar__right {
	@include media(400px) {
		column-gap: 10px;
	}

	display: flex;
	align-items: center;
	column-gap: 25px;

	.bars {
		display: block;
		border: none;
		background: local;
		width: 42px;
		height: 42px;

		img {
			width: 100%;
			height: 100%;
		}
	}
}

.navbar-right__network {
	display: flex;
	align-items: center;
	column-gap: 10px;

	a {
		width: 42px;
		height: 42px;
		display: block;

		&:first-child {
			@include media(850px) {
				border: 1.5px solid #ffff;
				border-radius: 100%;
			}
		}

		@include media(450px) {
			width: 35px;
			height: 35px;
		}

		img {
			width: 100%;
			height: 100%;
		}

		&:nth-child(2) {
			width: 48px;
			height: 48px;

			@include media(450px) {
				width: 42px;
				height: 42px;
			}
		}
	}
}

.nav-link {
	display: flex;
	align-items: center;
	justify-content: space-between;

	@include media(850px) {
		display: none;
	}

	.link-item {
		list-style: none;

		.item__link {
			text-decoration: none;
			color: #FFF;
			font-family: 'EurostileExtended-Roman-DTC', sans-serif;
			font-size: 18px;
			font-weight: 5;
			text-transform: capitalize;
		}

		.header-logo {
			display: block;
		}
	}

	.link-contact {
		display: flex;
		align-items: center;
		column-gap: 25px;
	}
}

// header end

// home start
#home {
	padding: calc(177px + 80px) 0 100px 0;
	background: url(../img/home-bg.png) no-repeat;
	background-size: cover;
	background-position: right;
	position: relative;
	height: 100vh;

	@include media(1000px) {
		background-position-x: 70%;
	}

	@include media(650px) {
		padding: 0;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: start;
	}

	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(110deg, rgba(32, 32, 32, 0.80) 22.75%, rgba(32, 32, 32, 0.00) 100%);
		z-index: 1;
	}

	.home-wrap {
		position: relative;
		z-index: 3;

		.home-title {
			max-width: 700px;
			color: #FFF;
			font-family: $fontTwo;
			font-size: 38px;
			font-weight: 5;
			line-height: 150%;
			letter-spacing: 1.14px;
			text-transform: uppercase;

			@include media(650px) {
				line-height: 130%;
				font-size: 30px;
			}

			@include media(476px) {
				font-size: 20px;
				line-height: normal;
			}
		}

		.home-btn {
			cursor: pointer;
			margin-top: 50px;
			border-radius: 20px;
			border: 0.5px solid #FFF;
			padding: 12px 80px;
			transition: ease-in 0.2s;

			@include media(650px) {
				margin-top: 25px;
			}
		}

	}
}

.home__bottom {
	border-bottom: 1px solid #fff;
	max-width: 383px;
	position: relative;
	z-index: 3;
	margin-top: 100px;
	padding-bottom: 5px;

	p {
		color: #FFF;
		font-size: 14px;
		font-weight: 300;
		letter-spacing: 0.56px;
	}
}

// home end

// plate start
#plate {
	padding: 100px 0;
	padding-top: 150px;
	padding-bottom: 0;

	.plate-wrap {
		margin-top: 75px;
		border-radius: 20px;
		background: #FFF;
		padding: 80px;

		@include media(1070px) {
			padding: 30px;
		}

		@include media(520px) {
			padding: 50px 20px;
		}
	}

	@include media(520px) {
		padding: 50px 0;
		padding-bottom: 0px;
	}
}

h1.title {
	font-family: $fontTwo;
	font-size: 38px;
	font-weight: 5;
	letter-spacing: 1.14px;
	text-transform: uppercase;
	width: fit-content;
	position: relative;

	@include media(650px) {
		font-size: 30px;
	}

	&::after {
		content: '';
		position: absolute;
		bottom: -3px;
		left: 0;
		width: calc(100% + 25px);
		height: 1px;
		background-color: #000;

		@include media(476px) {
			display: none;
		}
	}
}

.big-number {
	margin: 0 auto;
	max-width: fit-content;
	border-radius: 10px;
	background: #000;
	display: flex;
	align-items: center;
	position: relative;
	z-index: 3;

	.car__number {
		display: flex;
		align-items: end;
		border-radius: 10px;
		border: 3px solid #000;
		height: 78px;
		background-color: #F8F8F8;
		padding: 0 10px;

		h1 {
			position: relative;
		}

		.harf {
			font-family: $fontThree;
			font-size: 70px;
			font-weight: 400;
			letter-spacing: 3.5px;
			text-transform: uppercase;
			height: 100%;
			cursor: pointer;

			@include media(410px) {
				font-size: 50px;
				display: flex;
				align-items: center;
			}

			.ac__class.num__content--active {
				font-size: 45px;

				@include media(410px) {
					font-size: 35px;
					transform: translateY(5px);
				}
			}
		}

		.min-number {
			display: flex;
			align-items: center;
			height: 100%;

			h1 {
				font-family: $fontThree;
				font-size: 70px;
				font-weight: 400;
				letter-spacing: 3.5px;
				text-transform: uppercase;
				height: 100%;
				cursor: pointer;

				@include media(410px) {
					font-size: 50px;
					display: flex;
					align-items: center;
				}
			}

		}

		.car__number-xx {
			color: #000;
			font-family: $fontThree;
			font-size: 45px;
			font-weight: 400;
			letter-spacing: 3.5px;
			text-transform: uppercase;
		}
	}

	.big-number__countr {
		background-color: #F8F8F8;
		width: 87px;
		height: 78px;
		flex-shrink: 0;
		display: flex;
		align-items: center;
		flex-direction: column;
		border-radius: 10px;
		border: 3px solid #000;
		border-left: none;
		padding: 10px;
		row-gap: 2px;
		position: relative;

		.number_menu {
			top: calc(100% + 16px);

			@include media(410px) {
				top: calc(100% + 18px);
			}
		}

		.top-num {
			font-family: $fontThree;
			font-size: 70px;
			font-weight: 400;
			letter-spacing: 3.5px;
			line-height: 35px;
			text-transform: uppercase;
			height: 100%;
			transform: translateY(11px);
			cursor: pointer;

			@include media(410px) {
				display: block;
				font-size: 50px;
				transform: translateY(9px);
			}
		}

		.ac__class.num__content--active {
			display: block;
			font-size: 42px;
			transform: translateY(-10px);

			@include media(410px) {
				font-size: 35px;
			}
		}

		.countr {
			display: flex;
			align-items: center;
			column-gap: 5px;

			h1 {
				line-height: 100%;
				color: #000;
				font-family: $inter;
				font-size: 16px;
				font-weight: 600;
				letter-spacing: 0.8px;
				text-transform: uppercase;
			}

			img {
				width: 28px;
				height: 14px;
				flex-shrink: 0;
			}
		}
	}
}

.number_menu {
	position: absolute;
	top: 100%;
	left: 50%;
	transform: translateX(-50%);
	border-radius: 0px 0px 5px 5px;
	border: 3px solid #000;
	background: #F8F8F8;
	display: none;
	z-index: 5;

	li {
		padding: 0 10px;
		text-align: center;
		list-style: none;
		color: #000;
		font-family: $fontThree;
		font-size: 30px;
		font-weight: 400;
		letter-spacing: 0.9px;
		text-transform: uppercase;
	}
}

.number_menu--close.active {
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 2;
	background: local;
}

.number_menu.active {
	display: block;
}

// plate end

// buy start
#buy {
	padding-top: 150px;
	background-color: #f0f0f0;

	.buy-wrap {
		margin-top: 50px;
	}

	@include media(520px) {
		padding-top: 100px;
	}
}

.buy-link {
	margin-top: 50px;
	display: flex;
	align-items: center;
	justify-content: end;
	column-gap: 13px;
	cursor: pointer;

	h1 {
		color: #000;
		font-family: $fontTwo;
		font-size: 38px;
		font-weight: 5;
		letter-spacing: 1.14px;
		text-transform: uppercase;
	}

	@include media(650px) {
		margin-top: 25px;

		img {
			width: 35px;
			height: 35px;
		}

		h1 {
			font-size: 25px;

		}
	}

	@include media(410px) {
		img {
			width: 27px;
			height: 27px;
		}

		h1 {
			font-size: 20px;
		}
	}
}

.buy-wrap__cards {
	.wrap__cards-card {
		width: 100%;
		border-radius: 20px;
		background: #FFF;
		padding: 30px;
		height: 100%;
		max-height: 100%;

		p {
			margin-top: 20px;
			color: #000;
			font-size: 14px;
			font-weight: 300;
			letter-spacing: 0.56px;
		}

		.card-img {
			width: 100%;
			height: 100%;

			img {
				width: 100%;
				height: 100%;
				object-fit: fill;
			}
		}
	}

	.only-img {
		padding: 0;
		width: 100%;
		height: 100%;
	}
}

h2.card-title {
	color: #000;
	font-family: $fontTwo;
	font-size: 30px;
	font-weight: 5;
	letter-spacing: 0.9px;
	text-transform: uppercase;
}

// buy end

// decoration start
#decoration {
	padding-top: 150px;

	@include media(520px) {
		padding-top: 100px;
	}

	.decoration__title {
		color: #000;
		font-family: $fontTwo;
		font-size: 28px;
		font-weight: 5;
		letter-spacing: 1.12px;

		mark {
			color: #FFF;
			font-family: $fontTwo;
			font-size: 32px;
			font-weight: 5;
			letter-spacing: 1.28px;
			background-color: #21422F;
			padding: 0 5px;
		}
	}

	.decoration-wrap {
		margin-top: 50px;

		.decoration-wrap__btn {
			color: #fff;
			width: 100%;
			transition: all 0.2s ease-in-out;
			padding: 13px 0;
			font-family: $fontTwo;
			font-size: 14px;
			font-weight: 5;
			letter-spacing: 0.56px;
			margin-top: 40px;
			background-color: #21422F;
			border-radius: 20px;
			background: #21422F;
			border: none;

			&:hover {
				background-color: #5d382e;
			}
		}
	}
}

.decoration-cards {

	.decoration-card {
		width: 100%;
		border-radius: 20px;
		border: 1px solid #C5C5C5;
		padding: 20px 20px 30px 20px;

		.card-img {
			width: 100%;
			height: 100%;
		}
	}
}

.decoration-card {
	transition: all 0.2s ease-in-out;

	.card-img {
		img {
			border-radius: 20px;
			width: 100%;
			height: 100%;
			object-fit: fill;
		}
	}

	.card-about {
		margin-top: 30px;
		display: flex;
		align-items: start;
		flex-direction: column;

		.card__btn {
			transition: all 0.2s ease-in-out;
			margin-top: 20px;
			border-radius: 20px;
			border: 0.5px solid #000;
			width: 100%;
			color: #000;
			font-size: 14px;
			font-weight: 400;
			letter-spacing: 0.56px;
			padding: 11px;
		}

		h5 {
			font-size: 14px;
			font-weight: 400;
			letter-spacing: 0.56px;
		}
	}

	&:hover {
		background-color: #fff;

		.card-about {
			.card__btn {
				background-color: #21422F;
				color: #fff;
			}
		}
	}
}

// decoration end

// contact start
#contact {
	padding: 50px 0;
	padding-top: 150px;

	@include media(520px) {
		padding-top: 100px;
	}

	.contact-wrap {
		background-color: #fff;
		border-radius: 20px;
		padding: 80px 30px;
		display: flex;
		align-items: start;
		justify-content: space-between;

		@include media(1240px) {
			flex-direction: column;
			align-items: start;
			row-gap: 25px;
		}

		@include media(520px) {
			padding: 30px 20px;
		}

		.contact__title-block {
			max-width: 306px;
		}
	}
}

.contact__title-block {
	h1.title {
		&::after {
			content: '';
			width: 315px;
		}
	}

	p {
		margin-top: 25px;
		color: #959595;
		font-size: 14px;
		font-weight: 400;
		letter-spacing: 0.56px;
	}
}

.contact-wrap__form {
	width: 100%;
	max-width: 653px;

	@include media(1240px) {
		max-width: 100%;
	}

	input {
		width: 100%;
		height: 42px;
		border-radius: 20px;
		border: 0.5px solid #000;
		background: local;
		color: #1c1c1c;
		font-size: 14px;
		font-weight: 300;
		letter-spacing: 0.56px;
		padding: 11px 20px;
		outline: none;

		&::placeholder {
			color: #9D9D9D;
			font-size: 14px;
			font-weight: 300;
			letter-spacing: 0.56px;
		}
	}

	textarea {
		resize: none;
		@extend input
	}

	.form-submit__btn {
		width: 100%;
		height: 42px;
		border: none;
		border-radius: 20px;
		background: #21422F;
		color: #FFF;
		font-family: $fontTwo;
		font-size: 14px;
		font-weight: 5;
		letter-spacing: 0.56px;

		&:hover {
			background-color: #5d382e;
		}
	}
}

// contact end

// footer start
#footer {
	background-color: #ffff;

	.footer-wrap {
		padding: 50px 0;
	}
}

.footer-bottom {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 12px 0;
	border-top: 1px solid #000;

	@include media(850px) {
		flex-direction: column;
		align-items: start;
		row-gap: 10px;
	}

	p {
		color: #959595;
		font-size: 14px;
		font-weight: 300;
		letter-spacing: 0.42px;
	}

	a {
		width: 152px;
		height: 44px;
		flex-shrink: 0;
		display: block;
	}
}

.footer-left {
	display: flex;
	align-items: start;
	flex-direction: column;
	row-gap: 20px;

	h5 {
		max-width: 230px;
		color: #959595;
		font-family: $fontTwo;
		font-size: 14px;
		font-weight: 5;
		letter-spacing: 0.42px;
		text-transform: uppercase;
	}

	.footer-left__links {
		display: flex;
		align-items: start;
		column-gap: 20px;

		@include media(520px) {
			display: none;
		}

		a {
			color: #000;
			font-family: $fontTwo;
			font-size: 14px;
			font-weight: 5;
			text-decoration: none;
		}
	}

	.footer-logo {
		width: 166.511px;
		height: 49.391px;
		flex-shrink: 0;
		display: block;

		img {
			width: 100%;
			height: 100%;
		}
	}
}

.footer-right {
	.footer-right__title {
		color: #959595;
		font-family: $fontTwo;
		font-size: 14px;
		max-width: 238px;
		font-weight: 5;
	}

	.footer-right__bottom-title {
		max-width: 100%;
		margin-top: 20px;

		span {
			color: #000;
		}
	}

	.footer-right__contact {
		display: flex;
		align-items: center;
		column-gap: 50px;
		margin: 20px 0;

		@include media(1240px) {
			column-gap: 20px;
		}

		@include media(520px) {
			margin: 10px 0;
			flex-direction: column;
			align-items: start;
			justify-content: center;
			row-gap: 10px;
		}

		.footer-right__call {
			color: #000;
			font-family: $fontTwo;
			font-size: 18px;
			font-weight: 5;
			text-decoration: none;
		}

		.footer-reight__social {
			display: flex;
			align-items: center;
			column-gap: 10px;

			@include media(520px) {
				a {
					display: block;
					width: 30px;
					height: 30px;

					img {
						width: 100%;
						height: 100%;
					}

					&:last-child {
						width: 36px;
						height: 36px;
					}
				}
			}

			a {
				display: block;
				text-decoration: none;
			}
		}

		.footer-right__backCall {
			display: flex;
			margin-left: auto;
			color: #FFF;
			font-size: 14px;
			font-weight: 400;
			letter-spacing: 0.56px;
			background-color: #21422F;
			text-decoration: none;
			border-radius: 20px;
			border: none;
			padding: 5px 25px;
			transition: ease-in-out 0.2s;

			@include media(1200px) {
				margin-left: 0;
			}

			&:hover {
				background-color: #5d382e;
			}
		}
	}
}

// footer end

// sale-table start
#sale-table {
	margin-top: 60px;
	overflow-x: scroll;

	.sale-table__container {
		max-width: calc(100% - 150px);
		margin: 0 auto;

		@include media(1070px) {
			max-width: 90%;
		}
	}

	&::-webkit-scrollbar {
		display: none;
	}
}

.sale-table__bottom-btn {
	margin-top: 40px;
	width: 100%;
	color: #FFF;
	font-family: Manrope;
	font-size: 14px;
	font-style: normal;
	font-weight: 400;
	line-height: normal;
	letter-spacing: 0.56px;
	background-color: #21422F;
	border: none;
	border-radius: 20px;
	padding: 10px;
	transition: ease-in-out 0.2s;

	&:hover {
		background-color: #5d382e;
	}
}

.sale-table__head {
	padding-bottom: 20px;
	border-bottom: 1px solid #C5C5C5;

	.row>div {
		display: flex;
		align-items: end;
	}

	.wrap-item-group {
		position: relative;
		cursor: pointer;

		.number_menu {
			position: absolute;
			top: calc(100% + 5px);
			overflow-y: scroll;
			height: 150px;
			left: 0;
			z-index: 3;
			transform: translateX(0px);

			&::-webkit-scrollbar {
				display: none;
			}

			li {
				cursor: pointer;
				font-size: 14px;
				line-height: 170%;
				text-align: start;
			}
		}
	}

	@include media(1070px) {
		min-width: 700px;
	}

	.head__title {
		color: #000;
		font-size: 16px;
		font-weight: 700;
		letter-spacing: 0.48px;
		text-transform: capitalize;
	}
}

.sale-table__body {
	@include media(1070px) {
		min-width: 700px;
	}

	.table__body-item {
		padding: 20px 0;
		border-bottom: 1px solid #C5C5C5;
		transition: ease-in-out 0.2s;

		&:hover {
			background-color: #21422F;

			.sale-table__region {
				color: #ffff;
			}

			.sale-teble__btn {
				background-color: #fff;
				color: #5d382e;
			}
		}

		.row>div {
			display: flex;
			align-items: center;
		}
	}
}

.item-car__number {
	border-radius: 5px;
	background-color: #000;
	width: fit-content;
	display: flex;

	.number__left {
		background-color: #F8F8F8;
		border-radius: 5px;
		border: 3px solid #000;
		padding: 0 10px;
		display: flex;
		align-items: end;
		column-gap: 5px;

		h2 {
			color: #000;
			font-family: $fontThree;
			font-size: 35px;
			font-weight: 400;
			letter-spacing: 1.2px;
			text-transform: uppercase;
		}

		.item-min__number {
			display: flex;
			align-items: end;

			h1 {
				color: #000;
				font-family: $fontThree;
				font-size: 40px;
				font-weight: 400;
				letter-spacing: 2px;
				text-transform: uppercase;
			}
		}
	}

	.number__right {
		border-radius: 5px;
		width: 50px;
		flex-shrink: 0;
		border: 3px solid #000;
		border-left: none;
		background-color: #F8F8F8;
		display: flex;
		align-items: center;
		flex-direction: column;

		.right-country {
			display: flex;
			align-items: center;

			h4 {
				color: #000;
				font-family: $inter;
				font-size: 10px;
				font-weight: 600;
				letter-spacing: 0.5px;
				text-transform: uppercase;
			}

			img {
				width: 16px;
				height: 8px;
				flex-shrink: 0;
			}
		}
	}
}

.sale-table__region {
	color: #000;
	font-size: 14px;
	font-weight: 400;
	letter-spacing: 0.56px;
}

.sale-table__price {
	@extend .sale-table__region;
}

.sale-teble__btn {
	border-radius: 20px;
	background: #21422F;
	border: none;
	color: #FFF;
	font-family: Manrope;
	font-size: 14px;
	font-style: normal;
	font-weight: 400;
	line-height: normal;
	letter-spacing: 0.56px;
	padding: 10px 0;
	width: 100%;
	transition: ease-in-out 0.2s;
}

// sale-table end